:root {
  --primary-green: #00af85;
}

.list-page {
  padding: 1rem;
  padding-bottom: 5rem; /* extra space for footer */
  background-color: #f9f9f9;
  min-height: 100vh;
  box-sizing: border-box;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.title {
  margin-top: 60px;
  margin-bottom: -5px;
  font-size: 2.5rem;
  font-weight: 600;
}

.camera-btn {
  color: var(--primary-green);
  font-weight: 500;
  text-decoration: none;
  font-size: 1.3rem;
}

.search-container {
  margin-bottom: 1rem;
}

.search-container input {
    width: 100%;
    padding: 0.6rem 0.6rem 0.6rem 2.5rem;
    font-size: 1rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    background-image: url("data:image/svg+xml,%3Csvg fill='gray' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10 2a8 8 0 105.29 14.29l4.7 4.7a1 1 0 001.42-1.42l-4.7-4.7A8 8 0 0010 2zm0 2a6 6 0 110 12A6 6 0 0110 4z'/%3E%3C/svg%3E");
    background-size: 18px;
    background-repeat: no-repeat;
    background-position: 0.8rem center;
}

.plate-table {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
}

.plate-header,
.plate-row {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  padding: 0.75rem 1rem;
  border-bottom: 1px solid #eee;
  font-size: 0.95rem;
}

.plate-header {
  background-color: #f2f2f2;
  font-weight: bold;
}

.notify-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #f2f2f2;
  text-align: center;
  padding: 0.75rem;
  font-size: 0.9rem;
  border-top: 1px solid #ddd;
  z-index: 90;
}

.notify-footer a {
  font-weight: 500;
  color: #007aff;
  text-decoration: none;
}

.add-btn {
  position: fixed;
  bottom: 4.5rem; /* above the footer */
  right: 1.5rem;
  background-color: var(--primary-green);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  border: none;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.plate-row-link {
  text-decoration: none;
  color: inherit;
}
